import './CartItem.css';

import React from 'react';

import DecreaseIcon from '../media/icons/Decrease_qty.svg';
import IncreaseIcon from '../media/icons/Increase_qty.svg';
import RemoveIcon from '../media/icons/Delete-item.svg';
import { addToCartAction, removeItemFromCartAction, setToastAction } from '../state/actions';
import { useDispatch } from 'react-redux';
import { Link } from 'react-router-dom';

function CartItem({
    id,
    title,
    coverImageUrl,
    unitPrice,
    unitMeasure,
    quantity,
    subTotal,
    maxOrderable,
}) {
    const [qty, setQty] = React.useState(quantity);
    const [isQtyUpdated, setIsQtyUpdated] = React.useState(false);
    let timeoutToFetchUpdatedCart = React.useRef(null);
    const dispatch = useDispatch();
    
    const getValidQuantity = (value) => {
        if (value < 1) {
            return 1;
        }
        if (value > maxOrderable) {
            return maxOrderable;
        }
        return value;
    }

    const initFetchUpdatedCartTimeout = React.useCallback(() => {
        // since the cart is calculated at the backend, each time cart is
        // updated, a fetch request is made. we have to wait some time for user
        // to add updated quantity before re-fetching the cart. Hence this
        // timeout. This waits sometime before updating actual state instead
        // of updating right when the user clicks + or -. This lets the user
        // to click + or - multiple times instead of re-rensering on every click.

        clearTimeout(timeoutToFetchUpdatedCart.current);
        timeoutToFetchUpdatedCart.current = window.setTimeout(() => {
            dispatch(addToCartAction({
                id: id,
                quantity: qty
            }));
            dispatch(setToastAction({
                text: "Cart Updated!",
                timeout: 2000
            }));
        }, 750);
        setIsQtyUpdated(false);
    }, [qty, dispatch, id]);

    React.useEffect(() => {
        if (isQtyUpdated) {
            initFetchUpdatedCartTimeout();
        }
    }, [qty, isQtyUpdated, initFetchUpdatedCartTimeout])
    
    React.useEffect(() => {
        setQty(quantity);
    }, [quantity])

    return (
        <div className="CartItem">
            <div className="img-ctr"
                style={{backgroundImage: `url('${coverImageUrl}')`}}
            >
                <button className="close-icon"
                    onClick={_ => {
                        dispatch(removeItemFromCartAction({
                            id: id
                        }));
                        dispatch(setToastAction({
                            text: "Cart Updated!",
                            timeout: 2000
                        }));
                    }}
                >
                    <img src={RemoveIcon} alt=""/>
                </button>
            </div>

            <p className="details">
                <Link to={"/products/" + title.replace(/ /g, "-") + "_" + id.toString()}>
                    <span className="title">{title} • {unitMeasure}</span>
                </Link>
                <br/>
                <span className="sub-total">
                    <span>Rs. {subTotal}/- </span>
                    <span className="sub-total-eqn"> ({unitPrice} × {quantity})</span>
                </span>
                <br/>
                <span className="max-qty">Max Qty: {maxOrderable}</span>
            </p>

            <div className="qty-input-ctr">
                <button className="inc-btn"
                    onClick={e => {
                        setQty(getValidQuantity(qty+1));
                        setIsQtyUpdated(true);
                    }}>
                    <img src={IncreaseIcon} alt=""/>
                </button>
                <input type="text" 
                    value={qty}
                    onChange={e => {
                        let value = parseInt(e.currentTarget.value, 10);
                        if (e.currentTarget.value === "") {
                            value = 1;
                        }
                        setQty(getValidQuantity(value));
                        setIsQtyUpdated(true);
                    }} />
                <button className="dec-btn"
                    onClick={e => {
                        setQty(getValidQuantity(qty-1));
                        setIsQtyUpdated(true);
                    }}>
                    <img src={DecreaseIcon} alt=""/>
                </button>
            </div>
        </div>
    )
}

export default CartItem
