import './Home.css';

import React from 'react';
import { useSelector, useDispatch } from 'react-redux';

import HomeHeader from '../components/HomeHeader';
import ProductCard from '../components/ProductCard';
import BottomNavbar from '../components/BottomNavbar';
import LoadingSpinner from '../components/LoadingSpinner';

import { 
    setProductsAction,
    setSearchTextAction,
} from '../state/actions';

import { fetchAllProducts } from '../api/fetchAllProducts';
import { HOST_URL } from '../api/urls';

import SearchIcon from '../media/icons/Search.svg';
import CloseIcon from '../media/icons/Close.svg';
import BottomSheet from '../components/BottomSheet';
import AddToCartSheetContent from '../components/AddToCartSheetContent';
import { useScrollRestorer } from '../utils/react-scroll-restorer';

function Home() {
    const [loading, setLoading] = React.useState(true);
    const [error, setError] = React.useState("");
    const [isAddToCartSheetVisible, setIsAddToCartSheetVisible] = React.useState(false);
    const [addToCartProductId, setAddToCartProductId] = React.useState(null);
    const searchText = useSelector(state => state.searchText);
    const products = useSelector(state => state.products);
    const cartCount = useSelector(state => state.cartItems.length);
    const dispatch = useDispatch();
    
    useScrollRestorer("homeScreenScrollY", [loading]);
    
    React.useEffect(() => {
        setLoading(true);
        setError("");
        fetchAllProducts(
            (res) => {
                setLoading(false);
                const data = res.data;
                
                if (data.error) {
                    setError(data.error);
                    return;
                }

                dispatch(setProductsAction(data.payload));
            },
            (err) => {
                setLoading(false);
                if (err.response?.data?.error) {
                    setError(err.response.data.error);
                    return;
                }
                // setError(JSON.stringify(err));
                setError(err.message);
            }
        );
    }, [dispatch]);

    return (
        <div className="Home">
            <HomeHeader />
            <h3 className="about-tagline">Home made delicacies</h3>
            <p className="how-to-points">
                • Tap on the Plus icon to add product to the cart. <br/>
                • Tap on the image to view more details.
            </p>

            <div className="search-bar-ctr">
                <input type="text" className="search-bar" placeholder="Search ..." 
                    value={searchText} onChange={e => dispatch(setSearchTextAction(e.currentTarget.value))}/>
                {searchText === "" ? 
                    <img src={SearchIcon} alt="" className="search-icon"
                        onClick={_ => {
                            document.querySelector(".search-bar").focus();
                        }}/>
                    : <img src={CloseIcon} alt="" className="search-icon" 
                        style={{cursor: 'pointer'}}
                        onClick={_ => {
                            dispatch(setSearchTextAction(""));
                            document.querySelector(".search-bar").focus();
                        }} />}
            </div>

            {loading ? <LoadingSpinner /> : null}

            {error !== "" ? <p className="err-text">{error}</p> : null}

            {!loading && error === "" ?
                products.map(product => 
                    searchText !== "" && !product.name.toLowerCase().includes(searchText.toLocaleLowerCase()) ?
                        null : <ProductCard 
                        key={product.id}
                        id={product.id}
                        title={product.name}
                        coverImageUrl={HOST_URL + product.cover_image}
                        measure={product.unit_measure}
                        price={product.unit_price}
                        addBtnOnClick={(e) => {
                            setIsAddToCartSheetVisible(true);
                            setAddToCartProductId(product.id);
                        }}
                    />)
            : null}

            <BottomNavbar active="home" cartCount={cartCount} />

            <BottomSheet isVisible={isAddToCartSheetVisible} 
                sheetTitle={"Add to Cart"} 
                closeHandler={(e) => {
                    setIsAddToCartSheetVisible(false);
                    setAddToCartProductId(null);
                }}>
                {addToCartProductId ? 
                    <AddToCartSheetContent 
                        product_id={addToCartProductId} 
                        addToCartBtnClickCallback={(e) => {
                            setIsAddToCartSheetVisible(false);
                            setAddToCartProductId(null);
                        }} /> 
                    : null}
            </BottomSheet>
        </div>
    )
}

export default Home
