import './OrderDetails.css';

import React from 'react'
import TopBar from '../components/TopBar';
import BottomNavbar from '../components/BottomNavbar';
import { Link, useHistory, useParams } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { useScrollRestorer } from '../utils/react-scroll-restorer';
import LoadingSpinner from '../components/LoadingSpinner';
import { fetchSingleOrder } from '../api/fetchSingleOrder';

import SuccessBadgeIcon from '../media/icons/Badge-Tick.svg';
import CopyIcon from '../media/icons/Copy.svg';
import HomeIcon from '../media/icons/Home.svg';
import DeleteIcon from '../media/icons/Delete.svg';
import { setIsNewOrderAction, setToastAction } from '../state/actions';

function OrderDetails() {
    const [loading, setLoading] = React.useState(true);
    const [error, setError] = React.useState("");
    const [order, setOrder] = React.useState(null);
    const dispatch = useDispatch();
    const { id } = useParams();
    const isNewOrder = useSelector(state => state.isNewOrder);
    const cartCount = useSelector(state => state.cartItems.length);
    const history = useHistory();

    useScrollRestorer("orderDetailsScreenScrollY", [loading]);
    
    const isIdExistsInLocalStorage = React.useCallback(() => {
        let savedOrders = window.localStorage.getItem("orders");
        savedOrders = savedOrders ? JSON.parse(savedOrders) : {};
        return savedOrders[id] ? true : false;
    }, [id]);

    const removeOrderFromLocalStorage = React.useCallback(() => {
        let savedOrders = window.localStorage.getItem("orders");
        savedOrders = savedOrders ? JSON.parse(savedOrders) : {};
        if (savedOrders[id]) {
            delete savedOrders[id];
        }
        window.localStorage.setItem("orders", JSON.stringify(savedOrders));
    }, [id]);
    
    React.useEffect(() => {
        return () => {
            dispatch(setIsNewOrderAction(false));
        }
    }, [dispatch]);

    React.useEffect(() => {
        if (isIdExistsInLocalStorage()) {
            setLoading(true);
            setError("");
            fetchSingleOrder(
                id,
                (res) => {
                    setLoading(false);
                    const data = res.data;
                    
                    if (data.error) {
                        setError(data.error);
                        return;
                    }

                    setOrder(data.payload);
                },
                (err) => {
                    setLoading(false);

                    if (err.response?.data?.error) {
                        setError(err.response.data.error);
                        return;
                    }

                    setError(err.message);
                }
            );
        }
    }, [dispatch, id, isIdExistsInLocalStorage]);

    if (!isIdExistsInLocalStorage()) {
        if (error === "") {
            setError("It seems, either the Order ID is invalid or this Order was not submitted from this Device and can not be viewed here.");
        }
        return <div className="OrderDetails">
            <TopBar hideBackBtn={true}>
                <Link to="/">
                    <img src={HomeIcon} alt="" />
                    <span style={{marginLeft: '8px'}}>Home</span>
                </Link>
            </TopBar>

            {error !== "" ? <>
                <br/>
                <p className="err-text">
                    {error}
                    <br/>
                    <Link to="/" className="empty-cart-cta">Go to Home Screen</Link>
                </p>
            </> : null}
        </div>;
    };

    return (
        <div className={isNewOrder ? "OrderDetails" : "OrderDetails with-fixed-bottom-navbar"}>
            {isNewOrder 
                ? <TopBar hideBackBtn={true}>
                    <Link to="/">
                        <img src={HomeIcon} alt="" />
                        <span style={{marginLeft: '8px'}}>Home</span>
                    </Link>
                </TopBar>
                : <TopBar>
                    <button onClick={e => {
                        if (window.confirm("Are you sure you want to remove this Order from your Phone? You will not be able to view it afterwards.")) {
                            removeOrderFromLocalStorage();
                            dispatch(setToastAction({
                                text: "Order Removed!",
                                timeout: 2000
                            }));
                            history.replace("/orders");
                        }
                    }}>
                        <span>Remove from Phone</span>
                        <img src={DeleteIcon} alt="" />
                    </button>
                </TopBar>
            }

            {loading ? <LoadingSpinner /> : null}

            {error !== "" ? <>
                <br/>
                <p className="err-text">
                    {error}
                    <br/>
                    <Link to="/" className="empty-cart-cta">Go to Home Screen</Link>
                </p>
            </> : null}

            {!loading && error === "" && order !== null ? <>
                {isNewOrder 
                    ? <>
                        <img src={SuccessBadgeIcon} style={{display: 'block', margin: 'auto'}} alt=""/>
                        <p className="success-text">
                            Thank You! :)
                            <br/>
                            Order Placed Successfully!
                        </p>
                        {order.status === "PENDING CONFIRMATION" ? <p className="err-text" style={{marginTop: '4px'}}>
                            <span>We will call you soon to confirm your order.</span></p> : null}
                    </>
                    : <div style={{height: '4px'}}></div>
                }
                <div className="order-id-wpr">
                    <div className="order-id-ctr">
                        <h2>
                            {order.uf_id}
                        </h2>
                        <input type="text" readOnly={true} value={order.uf_id} id="orderIdCopyInput" style={{display: 'none'}} />
                        <button onClick={e => {
                            // https://www.w3schools.com/howto/howto_js_copy_clipboard.asp
                            /* Get the text field */
                            var copyText = document.getElementById("orderIdCopyInput");
                            copyText.style.display = 'block';

                            /* Select the text field */
                            copyText.select();
                            copyText.setSelectionRange(0, 99999); /*For mobile devices*/

                            /* Copy the text inside the text field */
                            document.execCommand("copy");
                            copyText.style.display = 'none';

                            dispatch(setToastAction({
                                text: "ID Copied!",
                                timeout: 2000
                            }))
                        }}>
                            <span>Copy ID</span>
                            <img src={CopyIcon} alt="" />
                        </button>
                    </div>
                        <p className="err-text order_id_help-text">
                            <strong>^ Order ID</strong>
                            <br/>
                            <span>Take a Screenshot of this or write it down.</span>
                        </p>
                </div>
                
                <p className="err-text status-text">
                    Placed On: <strong>{order.placed_on}</strong>
                    <br/>
                    Status: <strong>{order.status}</strong>
                    <br/>
                    <span>{!isNewOrder && order.status === "PENDING CONFIRMATION" ? "We will call you soon to confirm your order." : null}</span>
                </p>

                <div className="divider"></div>
                <p className="receipt-text text-center">Order Summary • {order.ordered_items.length} Item{order.ordered_items.length !== 1 ? "s" : null}</p>
                <div className="divider"></div>
                
                {order.ordered_items.map((product, index) => 
                    <div key={index}>
                        <div className="receipt-text-grp">
                            <p className="receipt-text">
                                <strong>{product.name}</strong>
                                <br/>
                                • {product.unit_measure} • Qty. {product.quantity}
                            </p>
                            <p className="receipt-text text-right">
                                Rs. {product.sub_total}/-
                            </p>
                        </div>
                        <div className="divider"></div>
                    </div>
                )}

                <div className="receipt-text-grp">
                    <p className="receipt-text" style={{marginBottom: '0px'}}>
                        Total:
                    </p>
                    <p className="receipt-text text-right" style={{marginBottom: '0px'}}>
                        Rs. {order.total}/-
                    </p>
                </div>
                <div className="receipt-text-grp">
                    <p className="receipt-text" style={{marginBottom: '0px'}}>
                        Tax ({order.tax_percentage}%):
                    </p>
                    <p className="receipt-text text-right" style={{marginBottom: '0px'}}>
                        Rs. {order.tax_amount}/-
                    </p>
                </div>
                <div className="receipt-text-grp">
                    <p className="receipt-text" style={{marginBottom: '0px'}}>
                        Delivery Charges:
                    </p>
                    <p className="receipt-text text-right" style={{marginBottom: '0px'}}>
                        Rs. {order.delivery_charges}/-
                    </p>
                </div>
                <div className="receipt-text-grp">
                    <p className="receipt-text">
                        <strong>GRAND TOTAL:</strong>
                    </p>
                    <p className="receipt-text text-right">
                        <strong>Rs. {order.grand_total}/-</strong>
                    </p>
                </div>

                <div className="divider"></div>
                <p className="receipt-text text-center">Delivery Details</p>
                <div className="divider"></div>

                <div className="delivery-details-items-ctr">
                    <div className="receipt-text-grp">
                        <p className="receipt-text hint-text">
                            Delivery Date:
                        </p>
                        <p className="receipt-text">
                            <strong>{order.delivery_date}</strong>
                        </p>
                    </div>
                    <div className="receipt-text-grp">
                        <p className="receipt-text hint-text">
                            Delivery Address:
                        </p>
                        <p className="receipt-text">
                            <strong>{order.delivery_address}</strong>
                        </p>
                    </div>
                    <div className="receipt-text-grp">
                        <p className="receipt-text hint-text">
                            Additional Comments:
                        </p>
                        <p className="receipt-text">
                            <strong>{order.additional_comments !== "" ? order.additional_comments : "-"}</strong>
                        </p>
                    </div>

                    <div className="divider"></div>
                    <p className="receipt-text text-center">Contact Details</p>
                    <div className="divider"></div>
                    
                    <div className="delivery-details-items-ctr">
                        <div className="receipt-text-grp">
                            <p className="receipt-text hint-text">
                                Name:
                            </p>
                            <p className="receipt-text">
                                <strong>{order.customer_name}</strong>
                            </p>
                        </div>
                        <div className="receipt-text-grp">
                            <p className="receipt-text hint-text">
                                Email:
                            </p>
                            <p className="receipt-text">
                                <strong>{order.customer_email}</strong>
                            </p>
                        </div>
                        <div className="receipt-text-grp">
                            <p className="receipt-text hint-text">
                                Mobile:
                            </p>
                            <p className="receipt-text">
                                <strong>{order.customer_phone}</strong>
                            </p>
                        </div>
                        <div className="divider"></div>
                    </div>
                </div>
                {isNewOrder
                    ? <Link to="/" className="empty-cart-cta" style={{width: '100%'}}>Go to Home Screen</Link>
                    : null
                }
            </> : null}

            {!isNewOrder ? <BottomNavbar cartCount={cartCount} /> : null}
        </div>
    )
}

export default OrderDetails
