import { act } from "react-dom/test-utils";
import { 
    SET_PRODUCTS,
    SET_SEARCH_TEXT,
    ADD_TO_CART,
    SET_TOAST,
    SET_CALCULATED_CART,
    REMOVE_ITEM_FROM_CART,
    CLEAR_CART,
    SET_SCROLL_Y,
    SET_PREFERENCES,
    SET_ORDER_ID_PARAM,
    SET_IS_NEW_ORDER,
} from './actions';

export const rootReducer = (state, action) => {
    let newState = {...state};
    
    if (action.type === SET_PRODUCTS) {
        newState.products = action.payload;
    }
    
    if (action.type === SET_SEARCH_TEXT) {
        newState.searchText = action.payload;
    }

    if (action.type === ADD_TO_CART) {
        let isItemExisted = false;
        const newCartItems = state.cartItems.map(item => {
            if (item.id === action.payload.id) {
                item.quantity = action.payload.quantity;
                isItemExisted = true;
            }
            return item;
        });
        if (!isItemExisted) {
            newCartItems.push(action.payload);
        }
        newState.cartItems = newCartItems;
    }

    if (action.type === SET_TOAST) {
        newState.toast = action.payload;
    }

    if (action.type === SET_CALCULATED_CART) {
        newState.calculatedCart = action.payload;
    }

    if (action.type === REMOVE_ITEM_FROM_CART) {
        const newCartItems = state.cartItems.filter(item => {
            return item.id !== action.payload.id;
        });
        newState.cartItems = newCartItems;
    }

    if (action.type === CLEAR_CART) {
        const newCartItems = [];
        newState.cartItems = newCartItems;
    }

    // if (action.type === SET_SCROLL_Y) {
    //     newState[action.payload.pageName] = action.payload.scrollY;
    // }

    if (action.type === SET_PREFERENCES) {
        newState.preferences = action.payload;
    }

    // if (action.type === SET_ORDER_ID_PARAM) {
    //     newState.orderIdParam = action.payload.id;
    // }

    if (action.type === SET_IS_NEW_ORDER) {
        newState.isNewOrder = action.payload;
    }

    window.localStorage.setItem("cartItems", JSON.stringify(newState.cartItems));
    return newState;
}
