import axios from 'axios';
import { RESEND_OTP } from './urls';

export const resendOtp = (order_id, successCallback, errorCallback) => {
    axios.patch(
        RESEND_OTP + order_id,
        {
            auth_id: "03c32d0c-5545-47d1-b579-88e439693501",
        }
    )
        .then(res => {
            successCallback(res);
        })
        .catch(err => {
            errorCallback(err);
        });
};
