import './HomeHeader.css';

import React from 'react'
import {Link} from 'react-router-dom';
import FlamoursLogo from '../media/Flamours_Logo.svg';
import FacebookLogoIcon from '../media/icons/facebook.svg';
import ChatIcon from '../media/icons/Chat.svg';

function HomeHeader() {
    return (
        <header className="HomeHeader">
            <a href="/">
                <img src={FlamoursLogo} alt="Flamour's"/>
            </a>
            <div className="icons-ctr">
                <Link to="/contact">
                    <img src={ChatIcon} alt="Contact"/>
                </Link>
                <a href="https://www.facebook.com/Flamours-624881878167019" target="_blank" rel="noreferrer">
                    <img src={FacebookLogoIcon} alt="Facebook"/>
                </a>
            </div>
        </header>
    )
}

export default HomeHeader
