import './BottomSheet.css';

import React from 'react'

import CloseIcon from '../media/icons/Close.svg';

function BottomSheet({
    children,
    isVisible,
    sheetTitle,
    closeHandler,
}) {
    const [isScrollable, setIsScrollable] = React.useState(false);

    React.useEffect(() => {
        if (isVisible) {
            setIsScrollable(false);
            window.setTimeout(() => {
                setIsScrollable(true);
            }, 210);
        }
    }, [isVisible])

    return (
        <div className={isVisible ? "BottomSheet visible" : "BottomSheet"}>
            <div className="bg-overlay" onClick={closeHandler}></div>
            <div className={isScrollable ? "bottom-sheet-wpr scrollable" : "bottom-sheet-wpr"}>
                <div className="close-overlay" onClick={closeHandler}></div>
                <div className="sheet">
                    <div className="sheet-title-ctr">
                        <img src={CloseIcon} alt="Close" style={{opacity: '0'}} />
                        <p className="sheet-title">{sheetTitle}</p>
                        <img className="close-icon" src={CloseIcon} alt="Close" onClick={closeHandler} />
                    </div>
                    {children}
                </div>
            </div>
        </div>
    )
}

export default BottomSheet
