import axios from 'axios';
import { SINGLE_ORDER } from './urls';

export const fetchSingleOrder = (order_uf_id, successCallback, errorCallback) => {
    axios.post(
        SINGLE_ORDER + "" + order_uf_id,
        {
            auth_id: "03c32d0c-5545-47d1-b579-88e439693501",
        }
    )
        .then(res => {
            successCallback(res);
        })
        .catch(err => {
            errorCallback(err);
        });
};
