import './Contact.css';
import React from 'react'
import TopBar from '../components/TopBar';
import BottomNavbar from '../components/BottomNavbar';

import CallIcon from '../media/icons/Call.svg';
import MessageIcon from '../media/icons/Message.svg';
import LocationIcon from '../media/icons/Location.svg';
import { fetchContact } from '../api/fetchContact';
import { useSelector } from 'react-redux';
import LoadingSpinner from '../components/LoadingSpinner';

function Contact() {
    const [contact, setContact] = React.useState({});
    const cartCount = useSelector(state => state.cartItems.length);
    const [loading, setLoading] = React.useState(true);
    const [error, setError] = React.useState("");

    React.useEffect(() => {
        setLoading(true);
        setError("");
        fetchContact(
            (res) => {
                setLoading(false);
                const data = res.data;
                
                if (data.error) {
                    setContact({});
                    setError(data.error);
                    return;
                }

                setContact(data.payload);
            },
            (err) => {
                setLoading(false);
                setContact({});
                if (err.response?.data?.error) {
                    setError(err.response.data.error);
                    return;
                }
                // setError(JSON.stringify(err));
                setError(err.message);
            }
        );
        window.scrollTo(0, 0);
    }, []);

    return (
        <div className="Contact">
            <TopBar />
            
            <p className="title-text-ctr">
                Contact
            </p>

            <div className="divider"></div>

            {loading ? <LoadingSpinner /> : null}

            {!loading && error === "" ? <>
                {contact.email && contact.email !== "" ? <p className="term-ctr">
                    <span className="num"><img src={MessageIcon} alt="Email"/></span>
                    <span className="point">{contact.email}</span>
                </p> : null}
                {contact.phone && contact.phone !== "" ? <p className="term-ctr">
                    <span className="num"><img src={CallIcon} alt="Phone"/></span>
                    <span className="point">{contact.phone}</span>
                </p> : null}
                {contact.address && contact.address !== "" ? <p className="term-ctr">
                    <span className="num"><img src={LocationIcon} alt="Address"/></span>
                    <span className="point">{contact.address}</span>
                </p> : null}
            </> : null}
            
            {error !== "" ? <p className="err-text">{error}</p> : null}

            <BottomNavbar cartCount={cartCount} />
        </div>
    )
}

export default Contact
