import './Checkout.css';

import React from 'react'
import TopBar from '../components/TopBar';
// import BottomNavbar from '../components/BottomNavbar';
import { Link, Redirect } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { fetchCalculatedCart } from '../api/fetchCalculatedCart';
import { useScrollRestorer } from '../utils/react-scroll-restorer';
import { clearCartAction, setCalculatedCartAction, setIsNewOrderAction, setPreferencesAction, setToastAction } from '../state/actions';
import LoadingSpinner from '../components/LoadingSpinner';
import { fetchPreferences } from '../api/fetchPreferences';
import { submitOrder } from '../api/submitOrder';
import { resendOtp } from '../api/resendOtp';
import { verifyOtp } from '../api/verifyOtp';

import EmptySquare from '../media/icons/Square.svg';
import TickSquareFilled from '../media/icons/TickSquare.svg';

function Checkout() {
    const [loading, setLoading] = React.useState(true);
    const [error, setError] = React.useState("");
    const [submittingOrder, setSubmittingOrder] = React.useState(false);
    const [submissionError, setSubmissionError] = React.useState("");
    const [submittingOtp, setSubmittingOtp] = React.useState(false);
    const [otpError, setOtpError] = React.useState("");
    const [resendTimer, setResendTimer] = React.useState(-1);
    const [isOtpResent, setIsOtpResent] = React.useState(false);
    const [isOtpVerified, setIsOtpVerified] = React.useState(false);
    const [isAgreedToTermsConditions, setIsAgreedToTermsConditions] = React.useState(false);
    const [order, setOrder] = React.useState(null);
    const dispatch = useDispatch();
    const cartItems = useSelector(state => state.cartItems);
    const cartCount = useSelector(state => state.cartItems.length);
    const calculatedCart = useSelector(state => state.calculatedCart);
    const preferences = useSelector(state => state.preferences);
    const timeoutRef = React.useRef(null);

    const [isSelfPickup, setIsSelfPickup] = 
        React.useState(window.localStorage.getItem("is_self_pickup")
            ? JSON.parse(window.localStorage.getItem("is_self_pickup")) : false);
    const delivery_address = React.useRef(null);
    const additional_comments = React.useRef(null);
    const customer_name = React.useRef(null);
    const customer_email = React.useRef(null);
    const customer_phone = React.useRef(null);

    useScrollRestorer("checkoutScreenScrollY", [loading]);

    React.useEffect(() => {
        if (cartItems.length > 0) {
            setLoading(true);
            setError("");
            fetchCalculatedCart(
                cartItems,
                (res) => {
                    setLoading(false);
                    const data = res.data;
                    
                    if (data.error) {
                        setError(data.error);
                        return;
                    }

                    dispatch(setCalculatedCartAction(data.payload));
                },
                (err) => {
                    setLoading(false);

                    if (err.response?.data?.error) {
                        setError(err.response.data.error);
                        return;
                    }

                    setError(err.message);
                },
                isSelfPickup
            );
            fetchPreferences(
                (res) => {
                    setLoading(false);
                    const data = res.data;
                    
                    if (data.error) {
                        setError(data.error);
                        return;
                    }

                    dispatch(setPreferencesAction(data.payload));
                },
                (err) => {
                    setLoading(false);

                    if (err.response?.data?.error) {
                        setError(err.response.data.error);
                        return;
                    }

                    setError(err.message);
                }
            );
        } else {
            setLoading(false);
        }
        
        window.scrollTo(0, 0);
    }, [cartItems, isSelfPickup, dispatch]);

    React.useEffect(() => {
        if (timeoutRef.current) {
            window.clearTimeout(timeoutRef.current);
            timeoutRef.current = null;
        }

        timeoutRef.current = window.setTimeout(() => {
            if (resendTimer > 0) {
                setResendTimer(resendTimer-1);
            }
        }, 1000);

        return () => {
            if (timeoutRef.current) {
                window.clearTimeout(timeoutRef.current);
                timeoutRef.current = null;
            }
        }
    }, [resendTimer]);

    React.useEffect(() => {
        if (delivery_address.current) {
            delivery_address.current.value = window.localStorage.getItem("delivery_address");
        }
        if (additional_comments.current) {
            additional_comments.current.value = window.localStorage.getItem("additional_comments");
        }
        if (customer_name.current) {
            customer_name.current.value = window.localStorage.getItem("customer_name");
        }
        if (customer_email.current) {
            customer_email.current.value = window.localStorage.getItem("customer_email");
        }
        if (customer_phone.current) {
            customer_phone.current.value = window.localStorage.getItem("customer_phone");
        }
    });

    React.useEffect(() => {
        window.localStorage.setItem("is_self_pickup", isSelfPickup);
    }, [isSelfPickup]);

    const submitOrderHandler = (e) => {
        e.preventDefault();

        if (!isAgreedToTermsConditions) {
            setSubmissionError("Please accept Terms & Conditions.");
            return;
        }

        setSubmittingOrder(true);

        const payload = {
            products: cartItems,
            delivery_details: {
                "delivery_date": e.currentTarget.elements.delivery_date.value,
                "is_self_pickup": isSelfPickup,
                "delivery_address": isSelfPickup
                    ? "(SELF PICKUP)"
                    : e.currentTarget.elements.delivery_address.value,
                "additional_comments": e.currentTarget.elements.additional_comments.value,
                "customer_name": e.currentTarget.elements.customer_name.value,
                "customer_email": e.currentTarget.elements.customer_email.value,
                "customer_phone": e.currentTarget.elements.customer_phone.value,
            }
        };

        submitOrder(
            payload,
            (res) => {
                setSubmittingOrder(false);
                const data = res.data;
                
                if (data.error) {
                    setSubmissionError(data.error);
                    return;
                }

                setOrder(data.payload);
                setResendTimer(30);
            },
            (err) => {
                setSubmittingOrder(false);

                if (err.response?.data?.error) {
                    setSubmissionError(err.response.data.error);
                    return;
                }

                setSubmissionError(err.message);
            }
        );
    };

    const resendOtpHandler = (e) => {
        setSubmittingOtp(true);
        resendOtp(
            order.id,
            (res) => {
                setSubmittingOtp(false);
                const data = res.data;
                
                if (data.error) {
                    setOtpError(data.error);
                    return;
                }

                setIsOtpResent(true);
                setResendTimer(30);
            },
            (err) => {
                setSubmittingOtp(false);

                if (err.response?.data?.error) {
                    setOtpError(err.response.data.error);
                    return;
                }

                setOtpError(err.message);
            }
        );
    };

    const verifyOtpHandler = (e) => {
        setSubmittingOtp(true);
        e.preventDefault();
        verifyOtp(
            order.id,
            e.currentTarget.elements.otp_code.value,
            (res) => {
                setSubmittingOtp(false);
                const data = res.data;
                
                if (data.error) {
                    setOtpError(data.error);
                    return;
                }

                dispatch(clearCartAction({}));
                setResendTimer(30);
                setIsOtpVerified(true);
                dispatch(setToastAction({
                    text: "Order Placed Successfully!",
                    timeout: 2000
                }));
            },
            (err) => {
                setSubmittingOtp(false);

                if (err.response?.data?.error) {
                    setOtpError(err.response.data.error);
                    return;
                }

                setOtpError(err.message);
            }
        );
    };

    const saveOrderIdToLocalStorage = () => {
        let savedOrders = window.localStorage.getItem("orders");
        savedOrders = savedOrders ? JSON.parse(savedOrders) : {};
        savedOrders[order.uf_id] = order.placed_on;
        window.localStorage.setItem("orders", JSON.stringify(savedOrders));
    };

    if (isOtpVerified) {
        saveOrderIdToLocalStorage();
        dispatch(setIsNewOrderAction(true));
        return <Redirect to={"/orders/" + order.uf_id} />
    }

    if (order !== null) {
        return (
            <div className="Checkout">
                <TopBar />

                {/* <h5 className="checkout-page-title">Checkout</h5>
                <p className="checkout-stats">
                    <span>{cartCount} {cartCount !== 1 ? "Items" : "Item"}</span> • 
                    <span> Rs. {calculatedCart.grand_total || 0}/-</span>
                </p> */}

                <p className="cart-items-count">
                    Checkout • <span>{cartCount} {cartCount !== 1 ? "Items" : "Item"}</span>
                </p>
                
                {loading ? <LoadingSpinner /> : null}
                
                {error !== "" ? <p className="err-text">{error}</p> : null}

                {!loading && error === "" && cartCount === 0 ? <>
                    <p className="err-text">
                        You haven't added anything to the cart yet!
                        <br/>
                        <Link to="/" className="empty-cart-cta">Start Adding</Link>
                    </p>
                </>: null}

                <div className="checkout-form-ctr">
                    <form action="" onSubmit={verifyOtpHandler}>
                        <div className="divider"></div>
                        <h5 className="checkout-page-title">Contact Verification</h5>
                        
                        <div className="form-group">
                            <label>
                                <strong>We have just sent a 6-digit Code on your Email and Mobile. Please enter that Code below.</strong>
                                <br/>
                                <span>• Make sure to check Spam folder of your Email as well.</span>
                            </label>
                            <input name="otp_code" required type="text" pattern="[0-9]{6}" placeholder="e.g. 123456"/>
                        </div>

                        {otpError !== "" ? <p className="err-text" style={{color: 'crimson'}}>{otpError}</p> : null}
                        
                        <div className="form-group">
                            {submittingOtp
                                ? <div style={{position: 'relative'}}><br/><LoadingSpinner /></div>
                                : <div>
                                    <input type="submit" value="Verify"/>
                                    <br/>
                                    {isOtpResent
                                        ? <p className="err-text">Code re-sent successfully! Please check your Email and Mobile again.</p>
                                        : resendTimer > 0
                                            ? <input type="button" value={"Resend (" + resendTimer + ")"} className="readonly"/>
                                            : <input onClick={resendOtpHandler} type="button" value={"Resend"}/>
                                    }
                                </div>}
                        </div>
                    </form>
                </div>
            </div>
        );
    }

    return (
        <div className="Checkout">
            <TopBar />

            {/* <h5 className="checkout-page-title">Checkout</h5>
            <p className="checkout-stats">
                <span>{cartCount} {cartCount !== 1 ? "Items" : "Item"}</span> • 
                <span> Rs. {calculatedCart.grand_total || 0}/-</span>
            </p> */}

            <p className="cart-items-count">
                Checkout •{" "}
                <span>
                    {cartCount} {cartCount !== 1 ? "Items" : "Item"}
                </span>
            </p>

            {loading ? <LoadingSpinner /> : null}

            {error !== "" ? <p className="err-text">{error}</p> : null}

            {!loading && error === "" && cartCount === 0 ? (
                <>
                    <p className="err-text">
                        You haven't added anything to the cart yet!
                        <br />
                        <Link to="/" className="empty-cart-cta">
                            Start Adding
                        </Link>
                    </p>
                </>
            ) : null}

            {!loading && error === "" && cartCount !== 0 && preferences !== null ? (
                <div className="checkout-form-ctr">
                    <form action="" onSubmit={submitOrderHandler}>
                        <div className="divider"></div>
                        <h5 className="checkout-page-title">Delivery Details</h5>

                        <div className="form-group">
                            <label>
                                <strong>Delivery Date (Required)</strong>
                                <br />
                                {preferences.settings?.min_advanced_booking_period !== 0 ? (
                                    <span>
                                        • Bookings are minimum {preferences.settings?.min_advanced_booking_period || 1}{" "}
                                        days in advance.
                                    </span>
                                ) : null}
                            </label>
                            <select name="delivery_date" required>
                                {preferences.available_dates.map((date) => {
                                    return (
                                        <option key={date} value={date}>
                                            {date}
                                        </option>
                                    );
                                })}
                            </select>
                        </div>

                        <div className="form-group">
                            
                        </div>

                        <div className="form-group self-pickup-check-ctr">
                            <label>
                                <strong>
                                    {isSelfPickup ? <span>Delivery Address</span> 
                                        : <span>Delivery Address (Required)</span>}
                                    <span 
                                        className="self-pickup-check"
                                        onClick={_ => setIsSelfPickup(!isSelfPickup)}>
                                        Self Pickup?
                                        {isSelfPickup ? <img src={TickSquareFilled} alt="Yes"/> 
                                            : <img src={EmptySquare} alt="No"/>}
                                    </span>
                                </strong>
                                <br />
                                {!isSelfPickup ? <>
                                    <span>• Deliveries are only available in Lahore.</span>
                                    </> : null}
                            </label>
                            {isSelfPickup 
                                ? <label className="pickup-address">{"(Self Pickup From) " + preferences.self_pickup_address}</label>
                                : <><textarea
                                    name="delivery_address"
                                    ref={delivery_address}
                                    onChange={(e) => window.localStorage.setItem("delivery_address", e.currentTarget.value)}
                                    maxLength="500"
                                    required
                                    rows="2"
                                    placeholder="e.g. Hs.#1, St.#2, Area, City."
                                ></textarea>
                                <p className="err-text text-right" style={{ marginTop: "2px" }}>
                                    <span>Max. 500 characters. English alphabets only.</span>
                                </p></>}
                        </div>

                        <div className="form-group">
                            <label>
                                <strong>Additional Comments (Optional)</strong>
                            </label>
                            <textarea
                                name="additional_comments"
                                ref={additional_comments}
                                onChange={(e) =>
                                    window.localStorage.setItem("additional_comments", e.currentTarget.value)
                                }
                                maxLength="500"
                                rows="2"
                                placeholder="e.g. No Sugar"
                            ></textarea>
                            <p className="err-text text-right" style={{ marginTop: "2px" }}>
                                <span>Max. 500 characters. English alphabets only.</span>
                            </p>
                        </div>

                        <div className="divider" style={{ marginTop: "32px" }}></div>
                        <h5 className="checkout-page-title">Contact Details</h5>

                        <div className="form-group">
                            <label>
                                <strong>Name (Required)</strong>
                            </label>
                            <input
                                name="customer_name"
                                ref={customer_name}
                                onChange={(e) => window.localStorage.setItem("customer_name", e.currentTarget.value)}
                                required
                                type="text"
                                placeholder="e.g. Ayesha Bilal"
                            />
                            <p className="err-text text-right" style={{ marginTop: "2px" }}>
                                <span>English alphabets only.</span>
                            </p>
                        </div>

                        <div className="form-group">
                            <label>
                                <strong>Email (Required)</strong>
                            </label>
                            <input
                                name="customer_email"
                                ref={customer_email}
                                onChange={(e) => window.localStorage.setItem("customer_email", e.currentTarget.value)}
                                required
                                type="email"
                                pattern="[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,}$"
                                placeholder="e.g. name@example.com"
                            />
                        </div>

                        <div className="form-group">
                            <label>
                                <strong>Mobile (Required)</strong>
                            </label>
                            <input
                                name="customer_phone"
                                ref={customer_phone}
                                onChange={(e) => window.localStorage.setItem("customer_phone", e.currentTarget.value)}
                                maxLength="11"
                                required
                                type="text"
                                pattern="[0-9]{11}"
                                placeholder="e.g. 03001234567"
                            />
                        </div>

                        <div className="divider" style={{ marginTop: "32px" }}></div>

                        <p className="cart-total">
                            <span>Total: </span>
                            <span className="text-right">Rs. {calculatedCart.total}/-</span>
                        </p>
                        <p className="cart-tax">
                            <span className="text-right">Tax {calculatedCart.tax_percentage}%: </span>
                            <span>Rs. {calculatedCart.tax_amount}/-</span>
                        </p>
                        {!isSelfPickup ? <p className="cart-tax">
                            <span>
                                Delivery Charges:
                                <br />
                                <small>
                                    (Free delivery above Rs. {preferences.settings.free_delivery_order_amount}/-)
                                </small>
                            </span>
                            <span className="text-right">Rs. {calculatedCart.delivery_charges}/-</span>
                        </p> : null}
                        <p className="cart-grand-total">
                            <span>GRAND TOTAL: </span>
                            <span className="text-right">Rs. {calculatedCart.grand_total}/-</span>
                        </p>

                        <div className="divider"></div>

                        <label className="terms-conditions-ctr"
                            onClick={(e) => setIsAgreedToTermsConditions(!isAgreedToTermsConditions)}
                        >
                            {isAgreedToTermsConditions 
                                ? <img src={TickSquareFilled} alt="(Yes)" />
                                : <img src={EmptySquare} alt="(No)" />}
                            I agree to the <Link to="/termsconditions">Terms &amp; Conditions</Link>.
                        </label>

                        {submissionError !== "" ? (
                            <p className="err-text" style={{ color: "crimson" }}>
                                {submissionError}
                            </p>
                        ) : null}

                        <div className="form-group" style={{ marginTop: "28px" }}>
                            {submittingOrder ? (
                                <div style={{ position: "relative" }}>
                                    <br />
                                    <LoadingSpinner />
                                </div>
                            ) : (
                                <input type="submit" value="Submit Order" />
                            )}
                        </div>
                    </form>
                </div>
            ) : null}
        </div>
    );
}

export default Checkout
