import axios from 'axios';
import { VERIFY_OTP } from './urls';

export const verifyOtp = (order_id, otp, successCallback, errorCallback) => {
    axios.patch(VERIFY_OTP + order_id + "/" + otp + "")
        .then(res => {
            successCallback(res);
        })
        .catch(err => {
            errorCallback(err);
        });
};
