import axios from 'axios';
import { SUBMIT_ORDER } from './urls';

export const submitOrder = (payload, successCallback, errorCallback) => {
    axios.post(
        SUBMIT_ORDER,
        {
            auth_id: "03c32d0c-5545-47d1-b579-88e439693501",
            ...payload,
        }
    )
        .then(res => {
            successCallback(res);
        })
        .catch(err => {
            errorCallback(err);
        });
};
