import './Cart.css';

import React from 'react'
import { useSelector, useDispatch } from 'react-redux';

import TopBar from '../components/TopBar';
import BottomNavbar from '../components/BottomNavbar';

import DeleteIcon from '../media/icons/Delete.svg';
import { clearCartAction, setCalculatedCartAction, setPreferencesAction, setToastAction } from '../state/actions';
import CartItem from '../components/CartItem';
import LoadingSpinner from '../components/LoadingSpinner';
import { fetchCalculatedCart } from '../api/fetchCalculatedCart';
import { HOST_URL } from '../api/urls';
import { Link } from 'react-router-dom';
import { useScrollRestorer } from '../utils/react-scroll-restorer';
import { fetchPreferences } from '../api/fetchPreferences';

function Cart() {
    const [loading, setLoading] = React.useState(true);
    const [error, setError] = React.useState("");
    const dispatch = useDispatch();
    const cartItems = useSelector(state => state.cartItems);
    const cartCount = useSelector(state => state.cartItems.length);
    const calculatedCart = useSelector(state => state.calculatedCart);
    const preferences = useSelector(state => state.preferences);
    const [isMinOrderableAmount, setIsMinOrderableAmount] = React.useState(false);

    useScrollRestorer("cartScreenScrollY", [loading]);

    React.useEffect(() => {
        if (cartItems.length > 0) {
            setLoading(true);
            setError("");
            fetchCalculatedCart(
                cartItems,
                (res) => {
                    setLoading(false);
                    const data = res.data;
                    
                    if (data.error) {
                        setError(data.error);
                        return;
                    }

                    dispatch(setCalculatedCartAction(data.payload));
                },
                (err) => {
                    setLoading(false);

                    if (err.response?.data?.error) {
                        setError(err.response.data.error);
                        return;
                    }

                    setError(err.message);
                }
            );
            fetchPreferences(
                (res) => {
                    setLoading(false);
                    const data = res.data;
                    
                    if (data.error) {
                        setError(data.error);
                        return;
                    }

                    dispatch(setPreferencesAction(data.payload));
                },
                (err) => {
                    setLoading(false);

                    if (err.response?.data?.error) {
                        setError(err.response.data.error);
                        return;
                    }

                    setError(err.message);
                }
            );
        } else {
            setLoading(false);
        }
        window.scrollTo(0, 0);
    }, [cartItems, dispatch]);

    React.useEffect(() => {
        if (calculatedCart && preferences) {
            setIsMinOrderableAmount(calculatedCart.total >= preferences.settings.min_order_amount_for_delivery);
        }
    }, [calculatedCart, preferences]);
    
    return (
        <div className="Cart">
            <TopBar>
                <button onClick={e => {
                    if (window.confirm("Are you sure you want to remove everything from cart?")) {
                        dispatch(clearCartAction({}));
                        dispatch(setToastAction({
                            text: "Cart Cleared!",
                            timeout: 2000
                        }));
                    }
                }}>
                    <span>Clear Cart</span>
                    <img src={DeleteIcon} alt="" />
                </button>
            </TopBar>

            <p className="cart-items-count">
                Cart • <span>{cartCount} {cartCount !== 1 ? "Items" : "Item"}</span>
            </p>
            
            {loading ? <LoadingSpinner /> : null}
            
            <div className="divider"></div>

            {error !== "" ? <p className="err-text">{error}</p> : null}
           
            {!loading && error === "" && cartCount === 0 ? <>
                <p className="err-text">
                    You haven't added anything to the cart yet!
                    <br/>
                    <Link to="/" className="empty-cart-cta">Start Adding</Link>
                </p>
            </>: null}

            {!loading && error === "" && cartCount !== 0 ?
                calculatedCart.items.map((product, index) => 
                    <div key={index}><CartItem 
                        id={product.id}
                        title={product.name}
                        coverImageUrl={HOST_URL + product.cover_image}
                        unitPrice={product.unit_price}
                        unitMeasure={product.unit_measure}
                        quantity={product.quantity}
                        subTotal={product.sub_total}
                        maxOrderable={product.max_orderable}
                    /><div className="divider"></div></div>)
            : null}

            {!loading && error === "" && cartCount !== 0 ? <>
                {/* <p className="cart-total">
                    <span>TOTAL: </span><span>Rs. {calculatedCart.total}/-</span>
                </p> */}
                <p className="cart-total">
                    <span>
                        <strong>TOTAL: </strong>
                        <br/>
                        <small>(Excl. of Tax &amp; Delivery)</small>
                    </span>
                    <span className="text-right">
                        <strong>Rs. {calculatedCart.total}/-</strong>
                    </span>
                </p>
                {/* <p className="cart-tax">
                    <span>TAX {calculatedCart.tax_percentage}%: </span><span>Rs. {calculatedCart.tax_amount}/-</span>
                </p>
                <p className="cart-grand-total">
                    <span>GRAND TOTAL: </span><span>Rs. {calculatedCart.grand_total}/-</span>
                </p> */}

                <div className="checkout-btn-ctr">
                    <Link to="/checkout" 
                        className={isMinOrderableAmount ? "checkout-btn" : "checkout-btn disabled"}
                        onClick={e => {
                            if (!isMinOrderableAmount) {
                                e.preventDefault()
                                dispatch(setToastAction({
                                    text: `Min. order amount is Rs. ${preferences.settings.min_order_amount_for_delivery}/-.`,
                                    timeout: 3000
                                }))
                            }
                        }}
                    >
                        Checkout
                    </Link>
                </div>
            </>: null}

            <BottomNavbar active="cart" cartCount={cartCount} />
        </div>
    )
}

export default Cart
