import './ProductDetails.css';

import React from 'react'
import { useSelector, useDispatch } from 'react-redux';
import { useParams } from "react-router-dom";

import ImagesSlider from '../components/ImagesSlider';
import BottomNavbar from '../components/BottomNavbar';
import TopBar from '../components/TopBar';
import LoadingSpinner from '../components/LoadingSpinner';
import BottomSheet from '../components/BottomSheet';
import AddToCartSheetContent from '../components/AddToCartSheetContent';

import { fetchSingleProduct } from '../api/fetchSingleProduct';

import ShareIcon from '../media/icons/Arrow-Up-Square.svg';
import AddIconWhite from '../media/icons/Add-White.svg';
import { HOST_URL } from '../api/urls';
import { setToastAction } from '../state/actions';

function ProductDetails() {
    const cartCount = useSelector(state => state.cartItems.length);
    const dispatch = useDispatch();
    const [loading, setLoading] = React.useState(true);
    const [error, setError] = React.useState("");
    const [shareableLink, ] = React.useState(window.location.href);
    const [isAddToCartSheetVisible, setIsAddToCartSheetVisible] = React.useState(false);
    const [addToCartProductId, setAddToCartProductId] = React.useState(null);
    const [product, setProduct] = React.useState(null);
    const { id } = useParams();

    React.useEffect(() => {
        setLoading(true);
        setError("");
        fetchSingleProduct(
            id,
            (res) => {
                setLoading(false);
                const data = res.data;
                
                if (data.error) {
                    setProduct(null);
                    setError(data.error);
                    return;
                }

                setProduct(data.payload);
            },
            (err) => {
                setLoading(false);
                setProduct(null);
                if (err.response?.data?.error) {
                    setError(err.response.data.error);
                    return;
                }
                // setError(JSON.stringify(err));
                setError(err.message);
            }
        );
        window.scrollTo(0, 0);
    }, [id]);

    return (
        <div className="ProductDetails">
            
            <TopBar>
                <button onClick={e => {
                    // https://www.w3schools.com/howto/howto_js_copy_clipboard.asp
                    /* Get the text field */
                    var copyText = document.getElementById("shareableLinkInput");
                    copyText.style.display = 'block';

                    /* Select the text field */
                    copyText.select();
                    copyText.setSelectionRange(0, 99999); /*For mobile devices*/

                    /* Copy the text inside the text field */
                    document.execCommand("copy");
                    copyText.style.display = 'none';

                    dispatch(setToastAction({
                        text: "Link Copied!",
                        timeout: 2000
                    }))
                }}>
                    <span>Copy Link</span>
                    <img src={ShareIcon} alt="" />
                </button>
            </TopBar>

            {loading ? <LoadingSpinner /> : null}

            {!loading && product && error === "" ? <>
                <ImagesSlider 
                    imageLinks={[
                        HOST_URL + product.cover_image,
                        ...product.product_images.map(image => HOST_URL + image.image)]}/>
                
                <h1 className="title">{product.name}</h1>

                <p className="price-details">{product.unit_measure} • Rs. {product.unit_price}/-</p>
                
                <p className="description">
                    {product.description
                        .replace("/\r\n/g", "\n")
                        .split("\n").map((line, index) => <span key={index}>{line}<br /></span>)}
                </p>

                <input type="text" readOnly={true} value={shareableLink} id="shareableLinkInput" style={{display: 'none'}} />
                
                <div className="add-to-cart-btn-ctr">
                    <button className="add-to-cart-btn" onClick={(e) => {
                            setIsAddToCartSheetVisible(true);
                            setAddToCartProductId(product.id);
                        }}>
                        <img src={AddIconWhite} alt="Add to Cart"/>
                    </button>
                </div>
            </> : null}

            {error !== "" ? <p className="err-text">{error}</p> : null}

            <BottomNavbar cartCount={cartCount} />

            <BottomSheet isVisible={isAddToCartSheetVisible} 
                sheetTitle={"Add to Cart"} 
                closeHandler={(e) => {
                    setIsAddToCartSheetVisible(false);
                    setAddToCartProductId(null);
                }}>
                {addToCartProductId ? 
                    <AddToCartSheetContent 
                        product_id={addToCartProductId} 
                        addToCartBtnClickCallback={(e) => {
                            setIsAddToCartSheetVisible(false);
                            setAddToCartProductId(null);
                        }} /> 
                    : null}
            </BottomSheet>
        </div>
    )
}

export default ProductDetails
