import './Orders.css';
import React from 'react'
import TopBar from '../components/TopBar';
import BottomNavbar from '../components/BottomNavbar';
import { useSelector } from 'react-redux';
import { Link } from 'react-router-dom';

import BackIcon from '../media/icons/Arrow-Left-2.svg';

function Orders() {
    const [orders, ] = React.useState((() => {
        let savedOrders = window.localStorage.getItem("orders");
        savedOrders = savedOrders ? JSON.parse(savedOrders) : {};
        return savedOrders;
    })());
    const [ordersCount, ] = React.useState(Object.keys(orders).length);
    const cartCount = useSelector(state => state.cartItems.length);

    return (
        <div className="Orders">
            <TopBar />
            <p className="title-text-ctr">
                My Orders • <span>{ordersCount}</span>
            </p>

            <div className="divider"></div>

            {ordersCount === 0 ? <>
                <p className="err-text">
                    You haven't placed any order yet!
                    <br/>
                    <Link to="/" className="empty-cart-cta">Start Ordering</Link>
                </p>
            </>: null}
            
            {ordersCount > 0 ? <>
                {Object.keys(orders).map(orderId => 
                    <Link to={"/orders/" + orderId} className="order-link" key={orderId}>
                        <span>
                            <strong className="title">{orderId}</strong>
                            <br/>
                            <span className="small-text">Placed on: <strong>{orders[orderId]}</strong></span>
                        </span>
                        <img src={BackIcon} alt=">"/>
                    </Link>
                )}
            </>: null}

            <BottomNavbar active="orders" cartCount={cartCount} />
        </div>
    )
}

export default Orders
