import './AddToCartSheetContent.css';

import React from 'react'
import { useSelector, useDispatch } from 'react-redux';

import { 
    addToCartAction,
    setToastAction,
} from '../state/actions';

import DecreaseIcon from '../media/icons/Decrease_qty.svg';
import IncreaseIcon from '../media/icons/Increase_qty.svg';

function AddToCartSheetContent({
    product_id,
    addToCartBtnClickCallback
}) {
    const [quantity, setQuantity] = React.useState(1);
    const product = useSelector(state => state.products.filter(product => product.id === product_id)[0]);
    const dispatch = useDispatch();
    
    const getValidQuantity = (value) => {
        if (value < 1) {
            return 1;
        }
        if (value > product.max_orderable) {
            return product.max_orderable;
        }
        return value;
    }

    return (
        <div className="AddToCartSheetContent">
            <h3 className="product-title">{product.name}</h3>
            <label htmlFor="qtyInputLabel" className="qty-label">Quantity: (Max {product.max_orderable})</label>
            <div className="quantity-input-ctr">
                <button className="dec-btn"
                    onClick={e => setQuantity(getValidQuantity(quantity-1))}>
                    <img src={DecreaseIcon} alt=""/>
                </button>
                <input id="qtyInputLabel" type="text" 
                    value={quantity}
                    onChange={e => {
                        let value = parseInt(e.currentTarget.value, 10);
                        if (e.currentTarget.value === "") {
                            value = 1;
                        }
                        setQuantity(getValidQuantity(value));
                    }} />
                <button className="inc-btn"
                    onClick={e => setQuantity(getValidQuantity(quantity+1))}>
                    <img src={IncreaseIcon} alt=""/>
                </button>
            </div>
            <button className="confirm-btn"
                onClick={e => {
                    dispatch(addToCartAction({
                        id: product.id,
                        quantity: quantity
                    }));
                    dispatch(setToastAction({
                        text: "Added to Cart!",
                        timeout: 2000
                    }))
                    addToCartBtnClickCallback();
                }}>
                <strong className="sub-total">PKR {product.unit_price * quantity}/-</strong>
                <span className="btn-text">Confirm</span>
            </button>
        </div>
    )
}

export default AddToCartSheetContent
