import axios from 'axios';
import { CALCULATED_CART } from './urls';

export const fetchCalculatedCart = (cartItems, successCallback, errorCallback, isSelfPickup = false) => {
    axios.post(
        CALCULATED_CART,
        {
            products: cartItems,
            is_self_pickup: isSelfPickup
        }
    )
        .then(res => {
            successCallback(res);
        })
        .catch(err => {
            errorCallback(err);
        });
};
