import './BottomNavbar.css';

import React from 'react'
import {Link} from 'react-router-dom';

import HomeIcon from '../media/icons/Home.svg';
import HomeFilledIcon from '../media/icons/Home_filled.svg';
import OrderIcon from '../media/icons/Document.svg';
import OrderFilledIcon from '../media/icons/Document_filled.svg';
import CartIcon from '../media/icons/Buy.svg';
import CartFilledIcon from '../media/icons/Buy_filled.svg';

function BottomNavbar({active, cartCount = 0}) {
    return (
        <nav className="BottomNavbar">
            <Link className="nav-item-ctr" to="/">
                {active === "home" ?
                    <img src={HomeFilledIcon} alt=""/>
                    : <img src={HomeIcon} alt=""/>}
                <br/>
                <span className={active === "home" ? "nav-link-text active" : "nav-link-text"}>Home</span>
            </Link>
            <Link className="nav-item-ctr prominent-item" to="/cart">
                <span className="prominent-icon-ctr">
                    <span className="prominent-icon-inner-ctr">
                        <span>{cartCount}</span>
                        <br/>
                        {active === "cart" ?
                            <img src={CartFilledIcon} alt=""/>
                            : <img src={CartIcon} alt=""/>}
                    </span>
                </span>
                <br/>
                <span className={active === "cart" ? "nav-link-text active" : "nav-link-text"}>Cart</span>
            </Link>
            <Link className="nav-item-ctr" to="/orders">
                {active === "orders" ?
                    <img src={OrderFilledIcon} alt=""/>
                    : <img src={OrderIcon} alt=""/>}
                <br/>
                <span className={active === "orders" ? "nav-link-text active" : "nav-link-text"}>Orders</span>
            </Link>
        </nav>
    )
}

export default BottomNavbar
