import './ImagesSlider.css';

import React from 'react'
// import { SRLWrapper } from "simple-react-lightbox";
// import { LightgalleryItem } from "react-lightgallery";
import "halkabox/dist/halkaBox.min.css";
import halkaBox from "halkabox";

function ImagesSlider({imageLinks}) {

    const removeHalkaBoxOverlay = () => {
        const overlay = document.querySelector(".hb-wrapper");
        overlay.parentNode.removeChild(overlay);
    };

    React.useEffect(() => {
        halkaBox.run("image-ctr");
        return () => {
            removeHalkaBoxOverlay();
        }
    }, []);

    return (
        <div className="ImagesSlider">
            {/* <SRLWrapper options={{
                settings: {
                    autoplaySpeed: 0,
                },
                buttons: {
                    showDownloadButton: false,
                }
            }}>
                {imageLinks.map((imageLink, index) => {
                    return <a 
                        href={imageLink} 
                        key={index}
                        className="image-ctr"
                        data-attribute="SRL"
                        style={{backgroundImage: `url('${imageLink}')`}}>
                            <img src={imageLink} alt="" style={{display: 'none'}} hidden />
                    </a>
                })}
            </SRLWrapper> */}
            {/* {imageLinks.map((imageLink, index) => {
                return <LightgalleryItem group="product_images_slider" key={index} src={imageLink} thumb={imageLink}>
                    <a 
                        href={imageLink} 
                        onClick={e => e.preventDefault()}
                        className="image-ctr"
                        style={{backgroundImage: `url('${imageLink}')`}}>
                    </a>
                </LightgalleryItem>
            })} */}
            {imageLinks.map((imageLink, index) => {
                return <a 
                    key={index}
                    href={imageLink} 
                    onClick={e => e.preventDefault()}
                    className="image-ctr"
                    style={{backgroundImage: `url('${imageLink}')`}}>
                </a>
            })}
        </div>
    )
}

export default ImagesSlider
