import './ProductCard.css';

import AddIconWhite from '../media/icons/Add-White.svg';
import { Link } from 'react-router-dom';

function ProductCard({
    id,
    title,
    coverImageUrl,
    measure,
    price,
    addBtnOnClick
}) {
    return (
        <article className="ProductCard">
            <Link to={"/products/" + title.replace(/ /g, "-") + "_" + id.toString()}
                className="img-ctr" style={{backgroundImage: `url('${coverImageUrl}')`}}>
            </Link>
            <div className="details-ctr">
                <Link to={"/products/" + title.replace(/ /g, "-") + "_" + id.toString()} className="details">
                    <span className="title">{title}</span>
                    <br/>
                    <span className="price-details">{measure} • Rs. {price}/-</span>
                </Link>
                <button className="add-to-cart-btn" onClick={addBtnOnClick}>
                    <img src={AddIconWhite} alt="Add to Cart"/>
                </button>
            </div>
        </article>
    );
}

export default ProductCard;
