import './TermsConditions.css';

import React from 'react'
import TopBar from '../components/TopBar';
import BottomNavbar from '../components/BottomNavbar';
import { useSelector } from 'react-redux';
import { fetchTermsAndConditions } from '../api/fetchTermsAndConditions';
import LoadingSpinner from '../components/LoadingSpinner';

function TermsConditions() {
    const [terms, setTerms] = React.useState([]);
    const cartCount = useSelector(state => state.cartItems.length);
    const [loading, setLoading] = React.useState(true);
    const [error, setError] = React.useState("");

    React.useEffect(() => {
        setLoading(true);
        setError("");
        fetchTermsAndConditions(
            (res) => {
                setLoading(false);
                const data = res.data;
                
                if (data.error) {
                    setTerms([]);
                    setError(data.error);
                    return;
                }

                setTerms(data.payload);
            },
            (err) => {
                setLoading(false);
                setTerms([]);
                if (err.response?.data?.error) {
                    setError(err.response.data.error);
                    return;
                }
                // setError(JSON.stringify(err));
                setError(err.message);
            }
        );
        window.scrollTo(0, 0);
    }, []);

    return (
        <div className="TermsConditions">
            <TopBar />
            
            <p className="cart-items-count">
                Terms &amp; Conditions
            </p>

            <div className="divider"></div>

            {loading ? <LoadingSpinner /> : null}

            {!loading && error === "" ? <>
                {terms.map((term, index) => <p className="term-ctr" key={index}>
                    <span className="num">{index+1}</span>
                    <span className="point">{term}</span>
                </p>)}
            </> : null}
            
            {error !== "" ? <p className="err-text">{error}</p> : null}

            <BottomNavbar cartCount={cartCount} />
        </div>
    )
}

export default TermsConditions
